import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Row, Col } from "antd";
import LayoutWrapper from '../../components/utility/layoutWrapper.js';
import Box from '../../components/utility/box';
import Switch from '../../components/uielements/switch';
import { InputSearch } from "../../components/uielements/input";
import Form from '../../components/uielements/form';
import FormClaim from './FormClaim';
import claimAction from '../../redux/claim/actions';
const { resetClaim } = claimAction;

const FormItem = Form.Item;

export class Recording extends Component {

  constructor(props) {
    super(props);
    resetClaim();
    this.state = {
      checked: false,
      claim: null
    };
  }


  componentDidUpdate(oldProps) {
    const newProps = this.props
    // console.log('newProps.claim', newProps.claim  )
    // console.log('this.state.claim', this.state.claim  )
    // console.log('this.state.checked', this.state.checked  )
    if(this.state.claim !== newProps.claim && this.state.checked === false) {
      console.log('set')
      this.setState({ claim: this.props.claim })
    }
  }

  onChange = () => {
    this.setState({
      checked: !this.state.checked,
    }, () => {
      if(this.state.checked === false) {
        console.log(this.state.checked)
        this.setState({
          claim: null,
        })
      }
    });
  }
  onSearch = claim => {
    this.setState({
      claim,
    });
  }
  render() {
    return (
      <LayoutWrapper>
        <Box>
          <Form onSubmit={this.handleSubmit}>
            <Row justify="start">
              <FormItem label="Existing Claim">
                <Col md={8} sm={8} xs={8} >

                    <Switch onChange={this.onChange} checked={this.state.checked} />

                </Col>
                <Col md={8} sm={8} xs={8}>
                  {this.state.checked?
                      <InputSearch
                        placeholder="Search on Claim ID"
                        onSearch={this.onSearch}
                      />
                  :null}
                </Col>
              </FormItem>
            </Row>
          </Form>
        </Box>

        <FormClaim claim = {this.state.claim}/>
      </LayoutWrapper>
    );
  }
}

export default connect(
  state => {
    return {auth: state.Auth, claim: state.Claim.currentClaim}
  },{resetClaim}
)(Recording);
