import React, { Component } from 'react';
import { connect } from 'react-redux';
import Box from '../../components/utility/box';
import Tabs, { TabPane } from '../../components/uielements/tabs';
//import FormValidation from './FormValidation';
import FormBasic from './FormBasic';
import FormDetails from './FormDetails';
import { Typography } from 'antd';

const { Title } = Typography;

export class NewClaim extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loggedIn: false,
      index: "1"
    }
  }



  render() {
    return (
      <Box>
        <Title>Claim ID: {this.props.claim}</Title>
        <Tabs activeKey={this.state.index} onChange = {(key)=>{
        	this.setState({ index: key });
        }}>
          <TabPane tab="Basic Info" key="1">
            <FormBasic claim = {this.props.claim}/>
          </TabPane>
          <TabPane tab="Claim Details" key="2" disabled = {this.props.claim?false:true}>
            <FormDetails />
          </TabPane>
          <TabPane tab="Basic Info" key="3">

          </TabPane>
        </Tabs>
      </Box>
    );
  }
}

export default connect(
  state => {
    return {auth: state.Auth}
  },{}
)(NewClaim);
