import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Input, Select, Row, Col, Divider  } from 'antd';
import DatePicker from '../../components/uielements/datePicker';
import Form from '../../components/uielements/form';
import Button from '../../components/uielements/button';
import Notification from '../../components/notification';
import claimAction from '../../redux/claim/actions';

const { Option } = Select;
const FormItem = Form.Item;
const { addNewClaim } = claimAction;

class FormWIthSubmissionButton extends Component {
  handleSubmit = e => {
    e.preventDefault();

    this.props.form.validateFieldsAndScroll((err, values) => {


      if (!err) {
        this.props.addNewClaim(values);
        Notification(
          'success',
          'Received values of form'
        );
      }
    });
  };

  componentDidUpdate(oldProps) {
    console.log(this.props)
    if (this.props.claim !==null) {
      fetch("http://localhost:9000/api/claimByID?id="+this.props.claim, {
        method: 'GET', // or 'PUT'
        headers:{
          'Content-Type': 'application/json'
        }
      }).then(res => res.json())
      .then(response => {
        //console.log(response.claimID)

      })
      .catch(error => console.error('Error:', error));
    }
  }


  render() {
    const { getFieldDecorator } = this.props.form;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 14 },
      },
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0,
        },
        sm: {
          span: 14,
          offset: 6,
        },
      },
    };
    return (
      <Form onSubmit={this.handleSubmit}>
        <Row gutter={16}>
          <Col span={12} >
            <Divider>Basic Info</Divider>
            <FormItem {...formItemLayout} label="Plan Name" hasFeedback>
            {getFieldDecorator('plan_name', {
              rules: [
                {
                  required: true,
                  message: 'Please input Plan Name!',
                },
              ],
            })(<Select initialValue="1" name="plan_name" id="plan_name">
                  <Option value="1">Major Medical</Option>
                  <Option value="2">Standard</Option>
                  <Option value="3">Comprehensive</Option>
                  <Option value="4">Fully Comprehensive</Option>
                </Select>)}
            </FormItem>
            <FormItem {...formItemLayout} label="Member / Policy Number" hasFeedback>
              {getFieldDecorator('member_policy_name', {
                rules: [
                  {
                    required: true,
                    message: 'Please input your member Policy Number!',
                  },
                ],
              })(<Input name="member_policy_name" id="member_policy_name" />)}
            </FormItem>
            <FormItem {...formItemLayout} label="Member Number" hasFeedback>
              {getFieldDecorator('membername', {
                rules: [
                  {
                    required: true,
                    message: 'Please input Member Name!',
                  },
                ],
              })(<Input name="membername" id="membername" />)}
            </FormItem>
            <FormItem {...formItemLayout} label="Mode of Treatment" hasFeedback>
              {getFieldDecorator('mode_of_treatment', {
                rules: [
                  {
                    required: true,
                    message: 'Please input Mode of Treatment!',
                  },
                ],
              })(<Select initialValue="1" name="mode_of_treatment" id="mode_of_treatment" >
                    <Option value="1">Inpatient</Option>
                    <Option value="2">Outpatient</Option>
                  </Select>)}
            </FormItem>
            <FormItem {...formItemLayout} label="Date of Hospitalisation" hasFeedback>
              {getFieldDecorator('date_of_hospitalisation_visit', {
                rules: [
                  {
                    required: true,
                    message: 'Please input Date of Hospitalisation!',
                  },
                ],
              })(<DatePicker name="date_of_hospitalisation_visit" id="date_of_hospitalisation_visit" />)}
            </FormItem>
            <FormItem {...formItemLayout} label="Cause of Hospitalisation / Visit" hasFeedback>
              {getFieldDecorator('cause_of_hospitalisation_visit', {
                rules: [
                  {
                    required: true,
                    message: 'Please input Cause of Hospitalisation / Visit!',
                  },
                ],
              })(<Input name="cause_of_hospitalisation_visit" id="cause_of_hospitalisation_visit" />)}
            </FormItem>
            <FormItem {...formItemLayout} label="Hospital Name & Address" hasFeedback>
              {getFieldDecorator('hospital_name_address', {
                rules: [
                  {
                    required: true,
                    message: 'Please input Hospital Name & Address!',
                  },
                ],
              })(<Input name="hospital_name_address" id="hospital_name_address" />)}
            </FormItem>
            <FormItem {...formItemLayout} label="Location" hasFeedback>
              {getFieldDecorator('location', {
                rules: [
                  {
                    required: true,
                    message: 'Please input Location!',
                  },
                ],
              })(<Select initialValue="1" name="location" id="location">
                    <Option value="1">US</Option>
                    <Option value="2">Thailand</Option>
                  </Select>)}
            </FormItem>
            <FormItem {...formItemLayout} label="Doctor Name" hasFeedback>
              {getFieldDecorator('doctor_name', {
                rules: [
                  {
                    required: true,
                    message: 'Please input Doctor Name!',
                  },
                ],
              })(<Input name="doctor_name" id="doctor_name" />)}
            </FormItem>
            <FormItem {...formItemLayout} label="Billing Currency" hasFeedback>
              {getFieldDecorator('billing_currency', {
                rules: [
                  {
                    required: true,
                    message: 'Please input Billing Currency!',
                  },
                ],
              })(<Select initialValue="1" name="billing_currency" id="billing_currency" >
                    <Option value="1">USD</Option>
                    <Option value="2">EUR</Option>
                    <Option value="3">THB</Option>
                  </Select>)}
            </FormItem>

          </Col>
          <Col span={12} >
            <Divider>Bank Info</Divider>
            <FormItem {...formItemLayout} label="Email Address" hasFeedback>
              {getFieldDecorator('email', {
                rules: [
                  {
                    required: true,
                    message: 'Please input your E-mail!',
                  },
                ],
              })(<Input name="email" id="email" />)}
            </FormItem>
            <FormItem {...formItemLayout} label="Phone Number" hasFeedback>
              {getFieldDecorator('phone', {
                rules: [
                  {
                    required: true,
                    message: 'Please input your Phone Number!',
                  },
                ],
              })(<Input name="phone" id="phone" />)}
            </FormItem>
            <FormItem {...formItemLayout} label="Home Address" hasFeedback>
              {getFieldDecorator('home_address', {
                rules: [
                  {
                    required: true,
                    message: 'Please input your Home Address!',
                  },
                ],
              })(<Input name="home_address" id="home_address" />)}
            </FormItem>
            <FormItem {...formItemLayout} label="Bank Account Number" hasFeedback>
              {getFieldDecorator('bank_account_number', {
                rules: [
                  {
                    required: true,
                    message: 'Please input your Bank Account Number!',
                  },
                ],
              })(<Input name="bank_account_number" id="bank_account_number" />)}
            </FormItem>
            <FormItem {...formItemLayout} label="Account Holder Name" hasFeedback>
              {getFieldDecorator('account_holder_name', {
                rules: [
                  {
                    required: true,
                    message: 'Please input your Account Holder Name!',
                  },
                ],
              })(<Input name="account_holder_name" id="account_holder_name" />)}
            </FormItem>
            <FormItem {...formItemLayout} label="Bank Name" hasFeedback>
              {getFieldDecorator('bank_name', {
                rules: [
                  {
                    required: true,
                    message: 'Please input your Bank Name!',
                  },
                ],
              })(<Input name="bank_name" id="bank_name" />)}
            </FormItem>
            <FormItem {...formItemLayout} label="Bank Address" hasFeedback>
              {getFieldDecorator('bank_address', {
                rules: [
                  {
                    required: true,
                    message: 'Please input your Bank Address!',
                  },
                ],
              })(<Input name="bank_address" id="bank_address" />)}
            </FormItem>
            <FormItem {...formItemLayout} label="SWIFT Code" hasFeedback>
              {getFieldDecorator('swift_address', {
                rules: [
                  {
                    required: true,
                    message: 'Please input your SWIFT Code!',
                  },
                ],
              })(<Input name="swift_address" id="swift_address" />)}
            </FormItem>
            <FormItem {...formItemLayout} label="(Optional)IBAN Code" hasFeedback>
              {getFieldDecorator('iban_code', {
                rules: [
                  {
                    message: 'Please input your IBAN Code!',
                  },
                ],
              })(<Input name="iban_code" id="iban_code" />)}
            </FormItem>
          </Col>
        </Row>
        <Row type="flex" justify="center">
          <FormItem {...tailFormItemLayout}>
            <Button type="primary" htmlType="submit">
              Submit
            </Button>
          </FormItem>
        </Row>
      </Form>
    );
  }
}

const WrappedFormWIthSubmissionButton = Form.create()(FormWIthSubmissionButton);

function mapStateToProps(state) {
  return {Auth:state.Auth };
}

export default connect(
  mapStateToProps,
  { addNewClaim }
)(WrappedFormWIthSubmissionButton);
